<template>
<div class="fix">
  <div class="orphanQueryBox">
    <table class="queryTable">
      <tr>
        <th class="queryHead">domain</th>
        <td class="queryBody">
          <input v-model="query.domain" list="domainList" style="float:left;" onkeydown="if (event.keyCode == 13) document.getElementById('query').click()"/>
          <datalist id="domainList">
            <option v-for="eachDomain in tag.domain" :key="eachDomain">{{eachDomain}}</option>
          </datalist>
          <button id="query" @click="queryData()" style="float:left;"><font-awesome-icon icon="magnifying-glass"/></button>
        </td>
        <th class="queryHead">cdn</th>
        <td class="queryBody">
          <Multiselect v-model="query.cdn" mode="tags" :options="tag.cdn" @select="queryData()" @deselect="queryData()" @clear="clear('cdn'); queryData();" :hideSelected="false" :searchable="true"/>
        </td>
      </tr>
    </table>
  </div>

  <div class="modeBox">
    <div style="background-color: white; width: 30%; height: 80%; margin-left: 5px; font-size: 25px; float: left;">
      <text style="color: black;">total: </text><text style="color: #058635;">{{total}}</text>
      <text style="color: black;"> showing: </text><text style="color: #058635;">{{currentStart}}</text><text style="color: black;"> to </text><text style="color: #058635;">{{currentEnd}}</text>
        per page:
      <select v-model="perPage" @change="currentPage = 1">
        <option value="50">50</option>
        <option value="100" selected>100</option>
        <option v-bind:value="total">ALL</option>
      </select>
    </div>
  </div>

  <div class="content">
    <table style="width:100%">
      <thead>
        <tr class="fixHeader">
          <th @click="sort('domain')" style="width: 30%">
            domain
            <template v-if="currentSort === 'domain'">
              <template v-if="currentSortDir === 'asc'">
                <font-awesome-icon icon="sort-up"/>
              </template>
              <template v-else>
                <font-awesome-icon icon="sort-down"/>
              </template>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort"/>
            </template>
          </th>
          <th style="width: 70%">
            cdn/gtm
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="data in sortedDatas.slice(startIndex, endIndex)" :key="data">
          <td>{{data.domain}}</td>
          <td>
            <text v-for="each in data.cdn" :key="each" class="tag" disabled>{{each.cdn}}</text>
          </td>
        </tr>
      </tbody>
    </table> 
  </div>

  <div class="foot">
    <button v-for="n in pageLength" :key="n" @click="currentPage = n" :class="{'currentPageButton': isCurrent(n), 'pageButton': !isCurrent(n) }">{{n}}</button>
  </div>

</div>
</template>

<script>
import Multiselect from '@vueform/multiselect';
import stickybits from 'stickybits'

export default {
  name: 'orphan',
  components: {
    Multiselect,
  },
  data () {
    return {
      query: {
        domain: null,
        cdn: [],
        archive: null
      },
      datas: [],
      tag: {},
      currentPage: 1,
      perPage: 100,
      currentSort: 'domain',
      currentSortDir: 'asc'
    }
  },
  computed: {
    total: function () {
      if (this.datas) {
        return this.datas.length
      } else {
        return 0
      }
    },
    pageLength: function () {
      if (this.datas) {
        return Math.ceil(this.datas.length / this.perPage)
      } else {
        return 0
      }
    },
    startIndex: function () {
      return ((this.currentPage - 1) * this.perPage)
    },
    endIndex: function () {
      return ((this.currentPage - 1) * this.perPage + new Number(this.perPage))
    },
    currentStart: function () {
      if (typeof(this.datas) === 'undefined') {
        return 0
      } else if (!this.datas.length) {
        return 0
      } else {
        return (this.startIndex + 1)
      }
    },
    currentEnd: function () {
      if (typeof(this.datas) === 'undefined') {
        return 0
      } else if (!this.datas.length) {
        return 0
      } else if (this.currentPage === this.pageLength) {
        return this.datas.length
      } else {
        return (this.endIndex)
      }
    },
    sortedDatas: function () {
      return this.datas.slice().sort((a,b) => {
        let modifier = 1;
        if(this.currentSortDir === 'desc') modifier = -1;
        if(a[this.currentSort] < b[this.currentSort]) return -1 * modifier;
        if(a[this.currentSort] > b[this.currentSort]) return 1 * modifier;
        return 0;
      });
    },
  },
  mounted() {
    let data = JSON.stringify(this.query);
    fetch(
      this.apiUrl + "getOrphanData/", {
      method: 'POST',
      headers: {
        "Content-Type": "application/json",
      },
      body: data
    })
    .then(response => response.json())
    .then((data) => {
      this.datas = data.datas;
      this.tag = data.tag;
    })
    .then(function() {
      stickybits('.fixHeader');
    })
  },
  methods: {
    clear(clearThis) {
      if (clearThis === "cdn") {
        this.query.cdn = [];
      }
    },
    resetQuery() {
      this.query = {
        domain: null,
        cdn: [],
        archive: null,
      }
    },
    queryData() {
      let data = JSON.stringify(this.query);
      this.currentPage = 1;
      this.perPage = 100;
      fetch(
        this.apiUrl + "getOrphanData/", { 
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: data
      })
      .then(response => response.json())
      .then((res) => {
        this.datas = res.datas;
        this.tag = res.tag;
        this.disableItem = [];
      })
      .then(function() {
        stickybits('.fixHeader');
      })
    },
    isCurrent(n) {
      if (this.currentPage === n) {
        return true
      } else {
        return false
      }
    },
    sort: function (s) {
      //if s == current sort, reverse
      if(s === this.currentSort) {
        this.currentSortDir = this.currentSortDir==='asc'?'desc':'asc';
      }
      this.currentSort = s;
    },
  }
}
</script>

