import { createRouter, createWebHashHistory } from 'vue-router'
import { VueCookieNext } from 'vue-cookie-next'
import index from '../components/index.vue'
import login from '../components/login.vue'
import show from '../components/show.vue'
import orphan from '../components/orphan.vue'
import domain from '../components/domain.vue'
import icp from '../components/icp.vue'
//import ip from '../components/ip.vue'
import log from '../components/log.vue'
import notFound from '../components/notFound.vue'
import whitelist from '../components/whitelist.vue'
import refreshDragon from '../components/refreshDragon.vue'
import certPurchase from '../components/certPurchase.vue'
import purgeCdnCache from '../components/purgeCdnCache.vue'

const routes = [
  {
    path: '/',
    name: 'index',
    component: index,
    beforeEnter: checkAuth
  },
  {
    path: '/domain',
    name: 'domain',
    component: domain,
    beforeEnter: checkAuth
  },
  {
    path: '/icp',
    name: 'icp',
    component: icp,
    beforeEnter: checkAuth
  },
/*  {
    path: '/ip',
    name: 'ip',
    component: ip,
    beforeEnter: checkAuth
  },*/
  {
    path: '/log',
    name: 'log',
    component: log,
    beforeEnter: checkAuth
  },
  {
    path: '/login',
    name: 'login',
    component: login,
  },
  {
    path: '/orphan',
    name: 'orphan',
    component: orphan,
    beforeEnter: checkAuth
  },
  {
    path: '/show',
    name: 'show',
    component: show,
    beforeEnter: checkAuth
  },
  {
    path: '/whitelist',
    name: 'whitelist',
    component: whitelist,
    beforeEnter: checkAuth
  },
  {
    path: '/refreshDragon',
    name: 'refreshDragon',
    component: refreshDragon,
    beforeEnter: checkAuth
  },
  {
    path: '/certPurchase',
    name: 'certPurchase',
    component: certPurchase,
    beforeEnter: checkAuth
  },
  {
    path: '/purgeCdnCache',
    name: 'purgeCdnCache',
    component: purgeCdnCache,
    beforeEnter: checkAuth
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'notFound',
    component: notFound,
    beforeEnter: checkAuth
  },
]


const router = createRouter({
  history: createWebHashHistory(),
  routes
})

function checkAuth(to, from, next) {
  let auth = VueCookieNext.getCookie('auth');
  if (auth === null) {
    next('/login');
  } else {
    fetch(
      //"http://192.168.2.202:8001/api/jwt/verify/", { //dev
      "http://it1-dragon.guardians.one:8000/api/jwt/verify/", { //prod
      method: 'POST',
      headers: { "Content-Type": "application/json" },
    })
    .then(function(response) {
      if (response.status !== 200) {
        alert("token invalid or expired");
        next('/login');
      } else {
        next();
      }
    })
  }
}

export default router
