<script>
import Multiselect from '@vueform/multiselect';
import { Toast } from 'bootstrap';

export default {
  name: 'whitelist',
  components: {
    Multiselect,
  },
  data () {
    return {
      tabIndex: 0,
      onQuery: false,
      searchIp: "",
      searchGroup: "",
      supportedDomainList: [],
      groupList: [],
      selectedDomain: null,
      currentWhitelist: [],
      whitelistIpGroup: [],
      rawInput: "",
      addOrRemove: "add",
      addIp: [],
      removeIp: [],
      selectedGroup: [],
      ipGroupDetail: null,
      addGroup: null,
      removeGroup: null,
      domainList: [],
    }
  },
  computed: {
    parsedInput: function () {
      return this.rawInput.replace(/[,;]/g, '\n').split('\n');
    },
    filteredIps () {
      return this.currentWhitelist.filter(item => {
        return item.toLowerCase().indexOf(this.searchIp.toLowerCase()) > -1;
      })
    },
    filteredGroups () {
      return this.groupList.filter(item => {
        return item.toLowerCase().indexOf(this.searchGroup.toLowerCase()) > -1;
      })
    },
  },
  watch: {
    rawInput: {
      handler: function() {
        if (this.addOrRemove === 'add') {
          this.addIp = this.rawInput.replace(/[,;]/g, '\n').split('\n');
          this.removeIp = [];
        }
        if (this.addOrRemove === 'remove') {
          this.removeIp = this.rawInput.replace(/[,;]/g, '\n').split('\n');
          this.addIp = [];
        }
      }
    },
    selectedGroup: {
      handler: function() {
        if (this.addOrRemove === 'add') {
          this.addGroup = this.selectedGroup;
          this.removeGroup = null;
        }
        if (this.addOrRemove === 'remove') {
          this.removeGroup = this.selectedGroup;
          this.addGroup = null;
        }
      },
      deep: true
    },
  },
  mounted() {
    fetch(
      this.apiUrl + "getSupportedDomainList/", {
      method: 'GET',
      headers: {
        "Content-Type": "application/json",
      },
    })
    .then(response => response.json())
    .then((data) => {
      this.supportedDomainList = data.datas;
    })
    .catch(error => alert('Error:', error));
    /*fetch(
      this.apiUrl + "getIpGroupList/", {
      method: 'GET',
      headers: {
        "Content-Type": "application/json",
      },
    })
    .then(response => response.json())
    .then((data) => {
      this.groupList = data.datas;
    })
    .catch(error => alert('Error:', error));*/
  },
  methods: {
    getCurrentWhitelist() {
      this.onQuery = true;
      fetch(
        this.apiUrl + "cdnWhitelist/?domain=" + this.selectedDomain, {
        method: 'GET',
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then(response => response.json())
      .then((data) => {
        this.currentWhitelist = data.currentWhitelist;
        this.whitelistIpGroup = data.whitelistIpGroup;
        this.onQuery = false;
      })
      .catch(error => alert('Error:', error));
    },
    updateWhitelist() {
      this.onQuery = true;
      let toast = new Toast(document.getElementById('successToast'), {animation: true, autohide: true, delay: 5000});
      let that = this;
      let data = {
        domain: this.selectedDomain,
        currentWhitelist: this.currentWhitelist,
        addIp: this.addIp,
        removeIp: this.removeIp,
        addGroup: this.addGroup,
        removeGroup: this.removeGroup
      }
      data = JSON.stringify(data);
      fetch(
        this.apiUrl + "cdnWhitelist/", {
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: data
      })
      .then(response => response.json())
      .then((res) => {
        if (res['res'] === "ok") {
          alert('update success\nDomain:\n' + that.selectedDomain + '\n\nAdd:\n' + that.addIp + '\n\nDelete:\n' + that.removeIp);
          that.rawInput = "";
          that.selectedGroup = [];
          toast.show();
          fetch(
            that.apiUrl + "cdnWhitelist/?domain=" + that.selectedDomain, {
            method: 'GET',
            headers: {
              "Content-Type": "application/json",
            },
          })
          .then(response => response.json())
          .then((data) => {
            that.currentWhitelist = data.currentWhitelist;
            that.whitelistIpGroup = data.whitelistIpGroup;
            that.onQuery = false;
          })
          .catch(error => alert('Error:', error));
        } else {
          alert("update fail\n" + res['res']);
          that.onQuery = false;
        }
      })
      .catch(error => alert('Error:', error));
    },
    multipleUpdateWhitelist() {
      this.onQuery = true;
      let that = this;
      let toast = new Toast(document.getElementById('successToast'), {animation: true, autohide: true, delay: 5000});
      let data = {
        domainList: this.domainList,
        addIp: this.addIp,
        removeIp: this.removeIp
      }
      data = JSON.stringify(data);
      fetch(
        this.apiUrl + "cdnWhitelist/", {
        method: 'PUT',
        headers: {
          "Content-Type": "application/json",
        },
        body: data
      })
      .then(response => response.json())
      .then((res) => {
        if (res['res'] === "ok") {
          alert('update success\nDomain:\n' + that.selectedDomain + '\n\nAdd:\n' + that.addIp + '\n\nDelete:\n' + that.removeIp);
          that.rawInput = "";
          toast.show();
          that.onQuery = false;
        } else {
          alert("update fail\n" + res['res']);
          that.onQuery = false;
        }
      })
      .catch(error => alert('Error:', error));
    },
    downloadCsv() {
      let filename = this.selectedDomain + '_whitelist.csv';
      let text = this.currentWhitelist.join('\n');
      let element = document.createElement('a');
      element.setAttribute('href', 'data:text/csv;charset=utf-8,' + encodeURIComponent(text));
      element.setAttribute('download', filename);

      element.style.display = 'none';
      document.body.appendChild(element);

      element.click();
      document.body.removeChild(element);  
    },
    showToast() {
      let toast = new Toast(document.getElementById('sentToast'), {animation: true, autohide: true, delay: 5000});
      toast.show();
    },
    linkClass(idx) {
      if (this.tabIndex === idx) {
        return ['bg-primary', 'text-light', 'font-weight-bold']
      } else {
        return ['text-muted']
      }
    },
    getIpGroupDetail(group) {
      fetch(
        this.apiUrl + "ipGroup/?name=" + group, {
        method: 'GET',
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then(response => response.json())
      .then((data) => {
        this.ipGroupDetail = data.datas;
      })
      .catch(error => alert('Error:', error));
    },
  }
}
</script>
