<template>
  <body>
    <div class="container">
      <div class="row">
        <div class="col-lg-3 col-md-2"></div>
          <div class="col-lg-6 col-md-8 login-box">
            <div class="col-lg-12 login-key">
              <i class="fa fa-key" aria-hidden="true"></i>
            </div>
            <div class="col-lg-12 login-title">
              CONTROL PANEL<br><br>
            </div>

            <div class="col-lg-12 login-form">
              <div class="col-lg-12 login-form">
                <div class="form-group">
                  <label class="form-control-label">USERNAME: </label>
                  <input type="text" class="form-control" v-model="username" onkeydown="if (event.keyCode == 13) document.getElementById('login').click()">
                </div>
                <div class="form-group">
                  <label class="form-control-label">PASSWORD: </label>
                  <input type="password" class="form-control" v-model="password" onkeydown="if (event.keyCode == 13) document.getElementById('login').click()">
                </div>
                <div class="col-lg-12 loginbttm">
                  <div class="col-lg-6 login-btm login-text">
                    <!-- Error Message -->
                  </div>
                  <div class="col-lg-6 login-btm login-button">
                    <button id="login" class="btn btn-outline-primary" @click="handleLogin">LOGIN</button>
                  </div>
                </div>
              </div>
            </div>
          <div class="col-lg-3 col-md-2"></div>
          </div>
      </div>
    </div>
  </body>
</template>

<script>
import { VueCookieNext } from 'vue-cookie-next'

export default {
  name: 'login',
  data () {
    return {
      username: '',
      password: '',
    }
  },
  methods: {
    handleLogin() {
      let that = this
      let data = {
        username: this.username,
        password: this.password
      };
      fetch(
        this.apiUrl + "dj-rest-auth/login/", {
        method: 'POST',
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data)
      })
      .then(function(response) {
        if (response.status === 401) {
          VueCookieNext.removeCookie("auth");
          alert("clear cookie and try again");
          throw new Error("An error occured");
        } else if (response.status !== 200) {
          VueCookieNext.removeCookie("auth");
          alert("username or password error");
          throw new Error("An error occured");
        } else {
          return response.json();
        }
      })
      .then(function() {
        let auth = "True";
        VueCookieNext.setCookie("auth", auth, {
          expire: '12h',
          path: '/',
          domain: '',
          secure: '',
          sameSite: '',
        });
      })
      .then(function() {
        that.$router.push('/');
      })
    }
  }
}
</script>


