<template>
<div class="container-fluid">  
  <div class="row">

    <div class="col-1 float-left border">
    </div>

    <div class="col border">
      <div>
        <b-tabs v-model="tabIndex" content-class="mt-3" style="margin-top: 10px;">

          <b-tab title="H5" :title-link-class="linkClass(0)">
            <div class="row justify-content-center align-items-center" style="margin-top: 10px; margin-bottom: 10px;">
              <div class="col">
                <table class="table table-bordered">
                  <thead>
                  </thead>
                  <tbody>
                    <tr>
                      <th scope="row">
                        dept
                      </th>
                      <td>
                        <Multiselect v-model="selectedDept" placeholder="Select department" :options="deptList" :searchable="true" :hideSelected="false" @clear="selectedDept=''" style="width: 80%; margin-left: 10%;" class="float-left"/>
                      </td>
                    </tr>
                    <tr v-if="selectedDept==='RD1-GO'">
                      <th scope="row">
                        srv
                      </th>
                      <td>
                        <Multiselect v-model="selectedSrv" placeholder="Select service" :options="srvList" :searchable="true" :hideSelected="false" @clear="selectedSrv=''" style="width: 80%; margin-left: 10%;" class="float-left">
                        </Multiselect>
                      </td>
                    </tr>
                    <tr>
                      <th scope="row">
                        env
                      </th>
                      <td>
                        <Multiselect v-model="selectedEnv" placeholder="Select environment" :options="envList" :searchable="true" :hideSelected="false" @clear="selectedEnv=''" style="width: 80%; margin-left: 10%;" class="float-left"/>
                      </td>
                    </tr>
                    <tr>
                      <td colspan="2">
                        <button class="btn btn-primary" @click="getH5DomainList()">
                          <font-awesome-icon icon="magnifying-glass"/>
                        </button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>

            <div class="row" style="margin-top: 10px; margin-bottom: 10px;">
              <div class="col-8">
                <table class="table table-bordered">
                  <thead>
                    <tr>
                      <th scope="row">
                        domain
                      </th>
                      <th scope="row">
                        dept
                      </th>
                      <th scope="row">
                        srv
                      </th>
                      <th scope="row">
                        env
                      </th>
                      <th scope="row">
                        cdn
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="eachDomain in rawH5DomainList" :key="eachDomain">
                      <td>
                        {{eachDomain.domain}}
                      </td>
                      <td>
                        <Multiselect class="multiselect-brown" v-model="eachDomain.department" mode="tags" :options="eachDomain.department" :hideSelected="false" :searchable="true" :createTag="true" :canClear="false" :caret="false" :disabled="true"/>
                      </td>
                      <td>
                        <Multiselect class="multiselect-blue" v-model="eachDomain.service" mode="tags" :options="eachDomain.service" :hideSelected="false" :searchable="true" :createTag="true" :canClear="false" :caret="false" :disabled="true"/>
                      </td>
                      <td>
                        <Multiselect class="multiselect-yellow" v-model="eachDomain.env" mode="tags" :options="eachDomain.env" :hideSelected="false" :searchable="true" :createTag="true" :canClear="false" :caret="false" :disabled="true"/>
                      </td>
                      <td>
                        <Multiselect class="multiselect-black" v-model="eachDomain.allCdn" mode="tags" :options="eachDomain.allCdn" :hideSelected="false" :searchable="true" :createTag="true" :canClear="false" :caret="false" :disabled="true"/>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div class="col-4">
                <div class="row justify-content-center align-items-center align-self-start" style="margin-top: 10px; margin-bottom: 10px;">
                  <div class="col-9">
                    <Multiselect class="float-left" v-model="type" placeholder="Select type" :options="typeList" :searchable="true" :hideSelected="false" :canClear="false" :canDeselect="false" style="margin-left: 5%;"/>
                  </div>
                  <div class="col-3">
                    <button type="button" class="btn btn-primary" style="font-size: 14px;" @click="purgeH5Cache()" :disabled="H5DomainList.length === 0 || (type === 'PATH' && rawInput === '') || onQuery">
                      <font-awesome-icon icon="arrows-rotate"/>
                    </button>
                  </div>
                </div>
                <div class="row justify-content-center align-items-center align-self-start" style="margin-top: 10px; margin-bottom: 10px;">
                  <div class="form-group">
                    <div class="col">
                      <textarea class="form-control" v-model="rawInput" placeholder="" rows="15" cols="60" :disabled="type === 'FULL'"></textarea>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </b-tab>

          <b-tab title="Multiple Domain Full Purge" :title-link-class="linkClass(1)">
            <div class="row justify-content-center align-items-center" style="margin-top: 10px; margin-bottom: 10px;">
              <div class="col">
                <Multiselect v-model="domainList" mode="tags" placeholder="Select domain" label="domain" trackBy="domain" valueProp="domain" :options="supportedDomainList" :close-on-select="false" :searchable="true" :hideSelected="false" @clear="domainList=[]" style="width: 80%; margin-left: 10%;" class="float-left">
                  <template v-slot:singlelabel="{ value }">
                    <div class="multiselect-single-label">
                      {{ value.domain }}
                      <button v-for="eachCdn in value.allCdn" :key="eachCdn" style="background-color: black; color: white; margin-left: 5px;" disabled>
                        {{ eachCdn }}
                      </button>
                    </div>
                  </template>
                  <template v-slot:option="{ option }">
                    {{ option.domain }}
                    <button v-for="eachCdn in option.allCdn" :key="eachCdn" style="background-color: black; color: white; margin-left: 5px;" disabled>
                      {{ eachCdn }}
                    </button>
                  </template>
                </Multiselect>
                <button type="button" class="btn btn-primary" style="font-size: 14px;" @click="purgeCache()" :disabled="domainList.length === 0 || onQuery">
                  <font-awesome-icon icon="arrows-rotate"/>
                </button>
              </div>
            </div>
          </b-tab>

        </b-tabs>
      </div>
    </div>

    <div class="col-1 border">
    </div>

  </div>
</div>
</template>

<script>
import Multiselect from '@vueform/multiselect';

export default {
  name: 'purgeCdnCache',
  components: {
    Multiselect,
  },
  data () {
    return {
      tabIndex: 0,
      onQuery: false,
      supportedDomainList: [],
      selectedDomain: "",
      rawInput: "",
      domainList: [],
      selectedDept: "",
      selectedSrv: "",
      selectedEnv: "",
      deptList: ["RD1-CQ", "RD1-GO", "RD1-GB"],
      srvList: ["AT", "BATTLE"],
      envList: ["PROD", "INT"],
      rawH5DomainList: [],
      H5DomainList: [],
      type: "PATH",
      typeList: ["FULL", "PATH"],
    }
  },
  computed: {
    path: function () {
      if (this.type === 'FULL') {
        return ["/"];
      }
      else {
        return this.rawInput.replace(/[,;]/g, '\n').split('\n');
      }
    }
  },
  watch: {
    selectedDept: {
      handler: function() {
        if (this.selectedDept !== 'RD1-GO') {
          this.selectedSrv = "";
        }
      }
    },
    /*type: {
      handler: function() {
        if (this.type === 'FULL') {
          this.path = ["/"];
        } 
        if (this.type === 'PATH') {
          this.path = this.rawInput.replace(/[,;]/g, '\n').split('\n'); 
        }
      }
    },*/
    rawH5DomainList: {
      handler: function() {
        if (this.rawH5DomainList.length > 0) {
          for (let index = 0; index < this.rawH5DomainList.length; ++index) {
            let element = this.rawH5DomainList[index];
            this.H5DomainList.push(element['domain']);
          }
        }
        else {
          this.H5DomainList = [];
        }
      }
    },
  },
  mounted() {
    fetch(
      this.apiUrl + "getPurgeDomainList/", {
      method: 'GET',
      headers: {
        "Content-Type": "application/json",
      },
    })
    .then(response => response.json())
    .then((data) => {
      this.supportedDomainList = data.datas;
    })
    .catch(error => alert('Error:', error));
  },
  methods: {
    purgeCache() {
      this.onQuery = true;
      let that = this;
      let data = {
        domainList: this.domainList,
      }
      data = JSON.stringify(data);
      fetch(
        this.apiUrl + "purgeCdnCache/", {
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: data
      })
      .then(response => response.json())
      .then((res) => {
        if (res['res'] === "ok") {
          alert('success');
          that.onQuery = false;
          that.domainList = [];
        } else {
          alert("purge fail\n" + res['res']);
          that.onQuery = false;
        }
      })
      .catch(error => alert('Error:', error));
    },
    getH5DomainList() {
      this.rawH5DomainList = [];
      let data = {
        selectedDept: this.selectedDept,
        selectedSrv: this.selectedSrv,
        selectedEnv: this.selectedEnv,
      }
      data = JSON.stringify(data);
      fetch(
        this.apiUrl + "getH5DomainList/", {
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: data
      })
      .then(response => response.json())
      .then((data) => {
        this.rawH5DomainList = data.datas;
      })
      .catch(error => alert('Error:', error));
    },
    purgeH5Cache() {
      this.onQuery = true;
      let that = this;
      let data = {
        H5DomainList: this.H5DomainList,
        path: this.path
      }
      data = JSON.stringify(data);
      fetch(
        this.apiUrl + "purgeH5CdnCache/", {
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: data
      })
      .then(response => response.json())
      .then((res) => {
        if (res['res'] === "ok") {
          alert('success');
          that.onQuery = false;
          that.rawH5DomainList = [];
          that.H5DomainList = [];
          that.rawInput = "";
        } else {
          alert("purge fail\n" + res['res']);
          that.onQuery = false;
        }
      })
      .catch(error => alert('Error:', error));
    },
    linkClass(idx) {
      if (this.tabIndex === idx) {
        return ['bg-primary', 'text-light', 'font-weight-bold']
      } else {
        return ['text-muted']
      }
    },
  }
}
</script>

<style scoped>
.multiselect-black {
  --ms-tag-bg-disabled: black;
  --ms-tag-color-disabled: white;
}

.multiselect-brown {
  --ms-tag-bg-disabled: #F5C68E;
  --ms-tag-color-disabled: #522E04;
}

.multiselect-blue {
  --ms-tag-bg-disabled: #DBEAFE;
  --ms-tag-color-disabled: #2563EB;
}

.multiselect-yellow {
  --ms-tag-bg-disabled: #F6F38C;
  --ms-tag-color-disabled: #7615D03;
}
</style>

