          </template>
        </th>
        <th @click="sort('relatedService')" style="width: 15%;">
          relatedSrv
          <template v-if="currentSort === 'relatedService'">
            <template v-if="currentSortDir === 'asc'">
              <font-awesome-icon icon="sort-up"/>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort-down"/>
            </template>
          </template>
          <template v-else>
