          </template>
        </th>
        <th @click="sort('relatedEnv')" style="width: 15%;">
          relatedEnv
          <template v-if="currentSort === 'relatedEnv'">
            <template v-if="currentSortDir === 'asc'">
              <font-awesome-icon icon="sort-up"/>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort-down"/>
            </template>
          </template>
          <template v-else>
            <font-awesome-icon icon="sort"/>
          </template>
        </th>
        <th @click="sort('relatedDomain')" style="width: 36%;">
          relatedDomain
          <template v-if="currentSort === 'relatedDomain'">
            <template v-if="currentSortDir === 'asc'">
              <font-awesome-icon icon="sort-up"/>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort-down"/>
            </template>
          </template>
          <template v-else>
            <font-awesome-icon icon="sort"/>
          </template>
        </th>
        <th @click="sort('checkTime')" style="width: 2%;">
          checkTime
          <template v-if="currentSort === 'checkTime'">
            <template v-if="currentSortDir === 'asc'">
              <font-awesome-icon icon="sort-up"/>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort-down"/>
            </template>
          </template>
          <template v-else>
            <font-awesome-icon icon="sort"/>
          </template>
        </th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(data, index) in sortedDatas.slice(startIndex, endIndex)" :key="index" v-bind:class="[{failRow : data.icp === 'FAIL'}, {yesRow: data.icp === 'YES'}]">
        <td>{{data.tld}}</td>

        <td>
          {{data.icp}}
          <!-- Button trigger modal -->
          <button type="button" data-bs-toggle="modal" data-bs-target="#descriptionModal" v-if="data.icp === 'FAIL'" @click="sendInfo(data, index)">
            <font-awesome-icon icon="pen"/>
          </button>
          
          <!-- Button trigger modal -->
