            </tr>
            <tr>
              <th>renewAuto</th>
              <td v-bind:style=" data.renewAuto === 'True' ? 'color: green;' : 'color: red;' ">{{data.renewAuto}}</td>
            </tr>
          </table>
        </td>

        <td>
          <Multiselect class="multiselect-black" v-model="data.relatedDns" mode="tags" :options="data.relatedDns" :hideSelected="false" :searchable="true" :createTag="true" :canClear="false" :caret="false" :disabled="true"/>
        </td>

