          <Multiselect class="multiselect-brown" v-model="data.relatedDepartment" mode="tags" :options="data.relatedDepartment" :hideSelected="false" :searchable="true" :createTag="true" :canClear="false" :caret="false" :disabled="true"/>
        </td>
        <td>
          <Multiselect class="multiselect-blue" v-model="data.relatedService" mode="tags" :options="data.relatedService" :hideSelected="false" :searchable="true" :createTag="true" :canClear="false" :caret="false" :disabled="true"/>
        </td>
        <td>
          <Multiselect class="multiselect-yellow" v-model="data.relatedEnv" mode="tags" :options="data.relatedEnv" :hideSelected="false" :searchable="true" :createTag="true" :canClear="false" :caret="false" :disabled="true"/>
        </td>

