              <th style="width: 25%;">dept</th>
              <th style="width: 25%;">srv</th>
              <th style="width: 25%;">env</th>
            </tr>
            <tr v-for="eachDomain in data.relatedDomain" :key="eachDomain">
              <td>{{eachDomain.domain}}</td>
              <td>
                <Multiselect class="multiselect-brown" v-model="eachDomain.department" mode="tags" :options="eachDomain.department" :hideSelected="false" :searchable="true" :createTag="true" :canClear="false" :caret="false" :disabled="true"/>
