<script>
import Multiselect from '@vueform/multiselect';
import stickybits from 'stickybits'

export default {
  name: 'domain',
  components: {
    Multiselect,
  },
  data () {
    return {
      query: {
        tld: null,
        icp: ['YES', 'FAIL'],
        registrar: [],
        privacy: null,
        renewAuto: null,
        unused: null,
        department: [],
        service: [],
        env: [],
      },
      datas: [],
      tag: {},
      disableItem: [],
      currentPage: 1,
      perPage: 100,
      selectedData: '',
      selectedIndex: '',
      currentSort: 'tld',
      currentSortDir: 'asc'
    }
  },
  computed: {
    total: function () {
      if (this.datas) {
        return this.datas.length
      } else {
        return 0
      }
    },
    pageLength: function () {
      if (this.datas) {
        return Math.ceil(this.datas.length / this.perPage)
      } else {
        return 0
      }
    },
    startIndex: function () {
      return ((this.currentPage - 1) * this.perPage)
    },
    endIndex: function () {
      return ((this.currentPage - 1) * this.perPage + new Number(this.perPage))
    },
    currentStart: function () {
      if (typeof(this.datas) === 'undefined') {
        return 0
      } else if (!this.datas.length) {
        return 0
      } else {
        return (this.startIndex + 1)
      }
    },
    currentEnd: function () {
      if (typeof(this.datas) === 'undefined') {
        return 0
      } else if (!this.datas.length) {
        return 0
      } else if (this.currentPage === this.pageLength) {
        return this.datas.length
      } else {
        return (this.endIndex)
      }
    },
    computeColor: function (icp) {
      if (icp === 'YES') {
        return 'green'
      } else if (icp === 'FAIL') {
        return 'red'
      } else {
        return 'black'
      }
    },
    sortedDatas: function () {
      return this.datas.slice().sort((a,b) => {
        let modifier = 1;
        if(this.currentSortDir === 'desc') modifier = -1;
        if(a[this.currentSort] < b[this.currentSort]) return -1 * modifier;
        if(a[this.currentSort] > b[this.currentSort]) return 1 * modifier;
        return 0;
      });
    },
  },
  mounted() {
    let data = JSON.stringify(this.query);
    fetch(
      this.apiUrl + "getIcpData/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: data
    })
    .then(response => response.json())
    .then((data) => {
      this.datas = data.datas;
      this.tag = data.tag;
    })
    .then(function() {
      stickybits('.fixHeader');
    })
  },
  methods: {
    clear(clearThis) {
      if (clearThis === "icp") {
        this.query.icp = null;
      }
      if (clearThis === "registrar") {
        this.query.registrar = [];
      }
      if (clearThis === "privacy") {
        this.query.privacy = null;
      }
      if (clearThis === "renewAuto") {
        this.query.renewAuto = null;
      }
      if (clearThis === "department") {
        this.query.department = [];
      }
      if (clearThis === "service") {
        this.query.service = [];
      }
      if (clearThis === "env") {
        this.query.env = [];
      }
    },
    resetQuery() {
      this.query = {
        tld: null,
        icp: null,
        registrar: [],
        privacy: null,
        renewAuto: null,
        unused: null,
        department: [],
        service: [],
        env: [],
      }
    },
    queryData() {
      this.currentPage = 1;
      this.perPage = 100;
      let data = JSON.stringify(this.query);
      fetch(
        this.apiUrl + "getIcpData/", {
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: data
      })
      .then(response => response.json())
      .then((res) => {
        this.datas = res.datas;
        this.tag = res.tag;
        this.disableItem = [];
      })
      .then(function() {
        stickybits('.fixHeader');
      })
    },
    sendInfo(data, index) {
      this.selectedData = data;
      this.selectedIndex = index;
    },
    updateDescription(tld, newDescription, index) {
      let data = {
        tld: tld,
        description: newDescription,
      }
      fetch(
        this.apiUrl + "icpDescription/", {
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(data)
      })
      .then(response => response.json())
      .then((res) => {
        this.datas[index] = res.data;
      })
    },
    isCurrent(n) {
      if (this.currentPage === n) {
        return true
      } else {
        return false
      }
    },
    sort: function (s) {
      //if s == current sort, reverse
      if(s === this.currentSort) {
        this.currentSortDir = this.currentSortDir==='asc'?'desc':'asc';
      }
      this.currentSort = s;
    },
  }
}
</script>

