<template>
<div class="container">

<h3>ICP備案查詢，請輸入一級域名</h3>

<div class="queryBox">
  <input v-model="queryTld" onkeydown="if (event.keyCode == 13) document.getElementById('query').click()"/>
  <button id="query" class="btn btn-primary" style="font-size: 14px;" @click="query(queryTld)" :disabled="queryTld === ''">query</button>
</div>

<div class="queryIcpContent">
  <table v-if="data?.info" class="queryIcpTable">
    <tr>
      <th>域名</th>
      <td>{{data.domain}}</td>
    </tr>
    <tr>
      <th>備案號</th>
      <td>{{data.info.icp}}</td>
    </tr>
    <tr>
      <th>資料時間</th>
      <td>{{data.info.time}}</td>
    </tr>
  </table>

  <table v-if="data?.message" class="queryIcpTable">
    <tr>
      <td style="color: red;">{{data.message}}</td>
    </tr>
  </table>
</div>

</div>
</template>

<script>
export default {
  name: 'icp',
  data () {
    return {
      queryTld: '',
      data: null,
    }
  },
  mounted() {
  },
  methods: {
    query(queryTld) {
      fetch(
        "https://api.vvhan.com/api/icp?url=" + queryTld
      )
      .then(response => response.json())
      .then((data) => {
        this.data = data;
        this.queryTld = '';
      });
    }
  }
}
</script>

<style scoped>
.queryIcpContent {
  top: 0px;
  bot: 0px;
  margin: auto;
  margin-top: 10px;
}

.queryIcpTable {
  margin: auto;
}

div {
  margin: auto;
  border: 0;
  padding: 0;
}

table, td, tr {
  border: 1px solid #333;
}

