<template>
<div class="queryBox">
  <table class="queryTable">
    <tr>
      <th class="queryHead">domain</th>
      <td class="queryBody">
        <input v-model="query.domain" list="domainList" style="float:left; width:70%;" onkeydown="if (event.keyCode == 13) document.getElementById('query').click()"/>
        <datalist id="domainList">
          <option v-for="eachDomain in tag.domain" :key="eachDomain">{{eachDomain}}</option>
        </datalist>
        <button id="query" @click="queryData()" style="float:left; width:20%"><font-awesome-icon icon="magnifying-glass"/></button>
      </td>
      <th class="queryHead">cdn</th>
      <td class="queryBody">
        <Multiselect v-model="query.cdn" mode="tags" :options="tag.cdn" @select="queryData()" @deselect="queryData()" @clear="clear('cdn'); queryData();" :hideSelected="false" :searchable="true"/>
      </td>
      <th class="queryHead">dept</th>
      <td class="queryBody">
        <Multiselect v-model="query.department" mode="tags" :options="tag.department" @select="queryData()" @deselect="queryData()" @clear="clear('department'); queryData();" :hideSelected="false" :searchable="true"/>
      </td>
      <th class="queryHead">srv</th>
      <td class="queryBody">
        <Multiselect v-model="query.service" mode="tags" :options="tag.service" @select="queryData()" @deselect="queryData()" @clear="clear('service'); queryData();" :hideSelected="false" :searchable="true"/>
      </td>
      <th class="queryHead">env</th>
      <td class="queryBody">
        <Multiselect v-model="query.env" mode="tags" :options="tag.env" @select="queryData()" @deselect="queryData()" @clear="clear('env'); queryData();" :hideSelected="false" :searchable="true"/>
      </td>
      <th style="width: 3%;">all</th>
      <td style="width: 3%;">
        <input type="checkbox" v-model="query.showAll" v-bind:true-value="'True'" @change="queryData()" style="width: 30px; height: 30px;"/>
      </td>
      <th class="queryHead" style="width: 3%; color: #F71503">alarm</th>
      <td style="width: 3%;">
        <input type="checkbox" v-model="query.alarm" v-bind:true-value="'True'" @change="queryData()" style="width: 30px; height: 30px;"/>
      </td>
      <td style="width: 6%;">
        <button @click="resetQuery(); queryData();">reset query</button>
      </td>
      <td>
        <a class="btn btn-secondary" data-bs-toggle="collapse" href="#collapseQuery" role="button" aria-expanded="false" aria-controls="collapseExample">
          advanced query
        </a>
      </td>
    </tr>
  </table>
</div>

<div class="collapse" id="collapseQuery">
  <table>
    <tr>
      <th class="queryHead">resource</th>
      <td class="queryBody">
        <input v-model="query.resource" style="float:left; width:70%;" onkeydown="if (event.keyCode == 13) document.getElementById('query').click()"/>
        <button @click="queryData()" style="float:left; width:20%;"><font-awesome-icon icon="magnifying-glass"/></button>
      </td>
      <th class="queryHead">dns</th>
      <td class="queryBody">
        <Multiselect v-model="query.dns" mode="tags" :options="tag.dns" @select="queryData()" @deselect="queryData()" @clear="clear('dns'); queryData();" :hideSelected="false" :searchable="true"/>
      </td>
      <th class="queryHead">vs</th>
      <td class="queryBody">
        <Multiselect v-model="query.vs" mode="tags" :options="tag.vs" @select="queryData()" @deselect="queryData()" @clear="clear('vs'); queryData();" :hideSelected="false" :searchable="true"/>
      </td>
      <th class="queryHead">pool</th>
      <td class="queryBody">
        <Multiselect v-model="query.pool" mode="tags" :options="tag.pool" @select="queryData()" @deselect="queryData()" @clear="clear('pool'); queryData();" :hideSelected="false" :searchable="true"/>
      </td>
