<!--
      <th class="queryHead">icp</th>
      <td class="queryBody">
        <Multiselect v-model="query.icp" mode="tags" :options="tag.icp" @select="queryData()" @deselect="queryData()" @clear="clear('icp'); queryData();" :hideSelected="false" :searchable="true"/>
      </td>
-->
    </tr>
  </table>
</div>

<div class="modeBox">
  <button class="modeButton" v-if="mode === 'detail'" @click="redraw('simple')" style="float: left;">
