          <template v-if="currentSort === 'env'">
            <template v-if="currentSortDir === 'asc'">
              <font-awesome-icon icon="sort-up"/>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort-down"/>
            </template>
          </template>
          <template v-else>
            <font-awesome-icon icon="sort"/>
          </template>
        </th>
        <th style="width: 3%">edit</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(data, index) in sortedDatas.slice(startIndex, endIndex)" :key="data">
        <td>
          {{data.domain}}
          <button style="float: right; width: 10%;" @click="checkDetail(data.domain)"><font-awesome-icon icon="list"/></button>
        </td>
        <td>
           <div v-if="data.jkb.health === 'ok' " class="led-green"></div>
           <div v-else-if="data.jkb.health === 'fail' " class="led-red"></div>
           <div v-else-if="data.jkb.health === 'standby' " class="led-grey"></div>
           <div v-else>X</div>
        </td>
        <td>
           <div v-if="data.gtmHealth === 'ok' " class="led-green"></div>
           <div v-else-if="data.gtmHealth === 'fail' " class="led-red"></div>
           <div v-else>X</div>
        </td>
        <td>
           <div v-if="data.gslbHealth === 'ok' " class="led-green"></div>
           <div v-else-if="data.gslbHealth === 'fail' " class="led-red"></div>
           <div v-else>X</div>
        </td>
        <td>
           <div v-if="data.vsHealth === 'ok' " class="led-green"></div>
           <div v-else-if="data.vsHealth === 'fail' " class="led-red"></div>
           <div v-else-if="data.vsHealth === 'standby' " class="led-grey"></div>
           <div v-else>X</div>
        </td>
<!--
        <td>
          <div v-if="data.icp === 'YES' " class="led-green"></div>
          <div v-else-if="data.icp === 'FAIL' " class="led-red"></div>
          <div v-else-if="data.icp === 'NO' " class="led-grey"></div>
          <div v-else>X</div>
        </td>
-->
        <td>
          <Multiselect v-model="data.department" mode="tags" :options="viableTag.department" :hideSelected="false" :searchable="true" :createTag="true" :canClear="false" :disabled="isDisable(index)"/>
        </td>
        <td>
          <Multiselect v-model="data.service" mode="tags" :options="viableTag.service" :hideSelected="false" :searchable="true" :createTag="true" :canClear="false" :disabled="isDisable(index)"/>
        </td>
        <td>
          <Multiselect v-model="data.env" mode="tags" :options="viableTag.env" :hideSelected="false" :searchable="true" :createTag="true" :canClear="false" :disabled="isDisable(index)"/>
        </td>
        <td>
          <button style="font-size: 14px;" @click="openEdit(index)" v-if="isDisable(index)">
            <font-awesome-icon icon="pen"/>
          </button>
          <button style="font-size: 14px;" @click="saveEdit(index, data.domain, data.department, data.service, data.env)" v-if="isDisable(index) === false">
            <font-awesome-icon icon="floppy-disk"/>
          </button>
        </td>
      </tr>
    </tbody>
  </table>
</div>

<div class="foot">
  <button v-for="n in pageLength" :key="n" @click="currentPage = n" :class="{'currentPageButton': isCurrent(n), 'pageButton': !isCurrent(n) }">{{n}}</button>
</div>

</template>

<script>
import Multiselect from '@vueform/multiselect';
import stickybits from 'stickybits'

export default {
  name: 'show',
  components: {
    Multiselect,
  },
  data () {
    return {
      query: {
        domain: null,
        resource: null,
        dns: [],
        cdn: [],
        department: [],
        service: [],
        env: [],
        icp: null,
        vs: [],
        pool: [],
        node: [],
        jkb: null,
        gtm: null,
        gtmMonitorType: [],
        alarm: "True",
        archive: null,
        showAll: null,
      },
      mode: "simple",
      datas: [],
      tag: {},
      viableTag: {},
      disableItem: [],
      currentPage: 1,
      perPage: 100,
      currentSort: 'domain',
      currentSortDir: 'asc'
    }
  },
  computed: {
    total: function () {
      if (this.datas) {
        return this.datas.length
      } else {
        return 0
      }
    },
    pageLength: function () {
      if (this.datas) {
        return Math.ceil(this.datas.length / this.perPage)
      } else {
        return 0
      }
    },
    startIndex: function () {
      return ((this.currentPage - 1) * this.perPage)
    },
    endIndex: function () {
      return ((this.currentPage - 1) * this.perPage + new Number(this.perPage))
    },
    currentStart: function () {
      if (typeof(this.datas) === 'undefined') {
        return 0
      } else if (!this.datas.length) {
        return 0
      } else {
        return (this.startIndex + 1)
      }
    },
    currentEnd: function () {
      if (typeof(this.datas) === 'undefined') {
        return 0
      } else if (!this.datas.length) {
        return 0
      } else if (this.currentPage === this.pageLength) {
        return this.datas.length
      } else {
        return (this.endIndex)
      }
    },
    sortedDatas: function () {
      return this.datas.slice().sort((a,b) => {
        let modifier = 1;
        if(this.currentSortDir === 'desc') modifier = -1;
        if(a[this.currentSort] < b[this.currentSort]) return -1 * modifier;
        if(a[this.currentSort] > b[this.currentSort]) return 1 * modifier;
        return 0;
      });
    },
  },
  mounted() {
    let that = this;
    let data = JSON.stringify(this.query);
    fetch(
      this.apiUrl + "getData/", {
      method: 'POST',
      headers: {
        "Content-Type": "application/json",
      },
      body: data
    })
    .then(response => response.json())
    .then((data) => {
      this.datas = data.datas;
      this.tag = data.tag;
      this.viableTag = data.viableTag;
    })
    .then(function() {
      stickybits('.fixHeader');
      if (that.query.alarm === 'True') {
        if (that.datas.length > 0) {
          const details = document.querySelectorAll("details");
          details.forEach((detail) => {
            detail.setAttribute("open", "True");
          });
        }
      }
    })
  },
  methods: {
    clear(clearThis) {
      if (clearThis === "dns") {
        this.query.dns = [];
      }
      if (clearThis === "cdn") {
        this.query.cdn = [];
      }
      if (clearThis === "department") {
        this.query.department = [];
      }
      if (clearThis === "service") {
        this.query.service = [];
      }
      if (clearThis === "env") {
        this.query.env = [];
      }
      if (clearThis == "icp") {
        this.query.icp = null;
      }
      if (clearThis === "vs") {
        this.query.vs = [];
      }
      if (clearThis === "pool") {
        this.query.pool = [];
      }
      if (clearThis === "node") {
        this.query.node = [];
      }
      if (clearThis === "gtmMonitorType") {
        this.query.gtmMonitorType = [];
      }
    },
    resetQuery() {
      this.query = {
        domain: null,
        resource: null,
        dns: [],
        cdn: [],
        department: [],
        service: [],
        env: [],
        vs: [],
        pool: [],
        node: [],
        jkb: null,
        gtm: null,
        gtmMonitorType: [],
        alarm: null,
        archive: null,
        showAll: null,
      }
    },
    queryData() {
      let that = this;
      this.currentPage = 1;
      this.perPage = 100;
      let data = JSON.stringify(this.query);
      fetch(
        this.apiUrl + "getData/", {
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: data
      })
      .then(response => response.json())
      .then((res) => {
        this.datas = res.datas;
        this.tag = res.tag;
        this.disableItem = [];
      })
      .then(function() {
        stickybits('.fixHeader');
        if (that.query.alarm === 'True') {
          if (that.datas.length > 0) {
            const details = document.querySelectorAll("details");
            details.forEach((detail) => {
              detail.setAttribute("open", "True");
            });
          }
        }
      })
    },
    isDisable(index) {
      if (this.disableItem[index] === false) {
        return false
      } else {
        return true
      }
    },
    openEdit(index) {
      this.disableItem[index] = false;
    },
    saveEdit(index, domain, department, service, env) {
      let data = {
        domain: domain,
        department: department,
        service: service,
        env: env
      }
      data = JSON.stringify(data);
      fetch(
        this.apiUrl + "modifyTag/", {
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: data
      })
      .then(response => response.json())
      .then((data) => {
        this.viableTag = data.newViableTag;
        this.disableItem[index] = true;
      })
    },
    checkDomain(index, domain) {
      let data = {
        domain: domain,
      }
      data = JSON.stringify(data);
      fetch(
        this.apiUrl + "checkDomain/", {
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: data
      })
      .then(response => response.json())
      .then((data) => {
        this.data[index].check = data.newCheck;
        this.disableItem[index] = true;
      })
    },
    redraw(newMode) {
      let that = this;
      this.mode = newMode;
      let p1 = new Promise( (resolve) => {
        resolve('Success!');
      } );
      p1.then(function() {
        stickybits('.fixHeader');
        if (that.query.alarm === 'True') {
          if (that.datas.length > 0) {
            const details = document.querySelectorAll("details");
            details.forEach((detail) => {
              detail.setAttribute("open", "True");
            });
          }
        }
      })
    },
    checkDetail(domain) {
      this.query.domain = domain;
      this.mode = 'detail';
      let that = this;
      let data = JSON.stringify(this.query);
      fetch(
        this.apiUrl + "getData/", {
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: data
      })
      .then(response => response.json())
      .then((res) => {
        this.datas = res.datas;
        this.tag = res.tag;
        this.disableItem = [];
      })
      .then(function() {
        stickybits('.fixHeader');
        if (that.query.alarm === 'True') {
          if (that.datas.length > 0) {
            const details = document.querySelectorAll("details");
            details.forEach((detail) => {
              detail.setAttribute("open", "True");
            });
          }
        }
      })
    },
    isCurrent(n) {
      if (this.currentPage === n) {
        return true
      } else {
        return false
      }
    },
    sort: function (s) {
      //if s == current sort, reverse
      if(s === this.currentSort) {
        this.currentSortDir = this.currentSortDir==='asc'?'desc':'asc';
      }
      this.currentSort = s;
    },
  }
}
</script>

