<template>
<div style="width: 100%;">
  <p style="color: red;">query施工中</p>
  <div style="width: 50%; float: left;">
    <datepicker v-model="from"/>
    {{from}}
  </div>
  <div style="width: 50%; float: left;">
    <datepicker v-model="to"/>
    {{to}}
  </div>
</div>

<div v-if="datas" class="content">
  <table>
    <thead>
      <tr class="fixHeader">
        <th @click="sort('api')" style="width: 5%;">
          api
          <template v-if="currentSort === 'api'">
            <template v-if="currentSortDir === 'asc'">
              <font-awesome-icon icon="sort-up"/>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort-down"/>
            </template>
          </template>
          <template v-else>
            <font-awesome-icon icon="sort"/>
          </template>
        </th>
        <th @click="sort('method')" style="width: 5%;">
          method
          <template v-if="currentSort === 'method'">
            <template v-if="currentSortDir === 'asc'">
              <font-awesome-icon icon="sort-up"/>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort-down"/>
            </template>
          </template>
          <template v-else>
            <font-awesome-icon icon="sort"/>
          </template>
        </th>
        <th @click="sort('userId')" style="width: 5%;">
          userId
          <template v-if="currentSort === 'userId'">
            <template v-if="currentSortDir === 'asc'">
              <font-awesome-icon icon="sort-up"/>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort-down"/>
            </template>
          </template>
          <template v-else>
            <font-awesome-icon icon="sort"/>
          </template>
        </th>
        <th @click="sort('userName')" style="width: 5%;">
          userName
          <template v-if="currentSort === 'userName'">
            <template v-if="currentSortDir === 'asc'">
              <font-awesome-icon icon="sort-up"/>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort-down"/>
            </template>
          </template>
          <template v-else>
            <font-awesome-icon icon="sort"/>
          </template>
        </th>
        <th @click="sort('group')" style="width: 5%;">
          group
          <template v-if="currentSort === 'group'">
            <template v-if="currentSortDir === 'asc'">
              <font-awesome-icon icon="sort-up"/>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort-down"/>
            </template>
          </template>
          <template v-else>
            <font-awesome-icon icon="sort"/>
          </template>
        </th>
        <th @click="sort('data')" style="width: 70%;">
          data
          <template v-if="currentSort === 'data'">
            <template v-if="currentSortDir === 'asc'">
              <font-awesome-icon icon="sort-up"/>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort-down"/>
            </template>
          </template>
          <template v-else>
            <font-awesome-icon icon="sort"/>
          </template>
        </th>
        <th @click="sort('timestamp')" style="width: 5%;">
          timestamp
          <template v-if="currentSort === 'timestamp'">
            <template v-if="currentSortDir === 'asc'">
              <font-awesome-icon icon="sort-up"/>
            </template>
            <template v-else>
              <font-awesome-icon icon="sort-down"/>
            </template>
          </template>
          <template v-else>
            <font-awesome-icon icon="sort"/>
          </template>
        </th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="data in sortedDatas.slice(startIndex, endIndex)" :key="data" class="spacingRow">
        <td>{{data.api}}</td>
        <td>{{data.method}}</td>
        <td>{{data.userId}}</td>
        <td>{{data.userName}}</td>
        <td>{{data.group}}</td>
        <td>{{data.data}}</td>
        <td>{{data.timestamp}}</td>
      </tr>
    </tbody>
  </table>
</div>

<div class="foot">
  <button v-for="n in pageLength" :key="n" @click="currentPage = n" :class="{'currentPageButton': isCurrent(n), 'pageButton': !isCurrent(n) }">{{n}}</button>
</div>
</template>

<script>
import { ref } from "vue";
import Datepicker from 'vue3-date-time-picker';
import 'vue3-date-time-picker/dist/main.css';
//import Multiselect from '@vueform/multiselect';
import stickybits from 'stickybits'

export default {
  name: 'log',
  components: {
    Datepicker,
  },
  data () {
    return {
      from: new Date(new Date().setDate(new Date().getDate() - 1)),
      to: ref(new Date()),
      selected: null,
      datas: [],
      currentPage: 1,
      currentSort: 'timestamp',
      currentSortDir: 'desc'
    }
  },
  computed: {
    total: function () {
      if (this.datas) {
        return this.datas.length
      } else {
        return 0
      }
    },
    pageLength: function () {
      if (this.datas) {
        return Math.ceil(this.datas.length / 100)
      } else {
        return 0
      }
    },
    startIndex: function () {
      return ((this.currentPage - 1) * 100)
    },
    endIndex: function () {
      return ((this.currentPage - 1) * 100 + 100)
    },
    currentStart: function () {
      if (typeof(this.datas) === 'undefined') {
        return 0
      } else if (!this.datas.length) {
        return 0
      } else {
        return (this.startIndex + 1)
      }
    },
    currentEnd: function () {
      if (typeof(this.datas) === 'undefined') {
        return 0
      } else if (!this.datas.length) {
        return 0
      } else if (this.currentPage === this.pageLength) {
        return this.datas.length
      } else {
        return (this.endIndex)
      }
    },
    sortedDatas: function () {
      return this.datas.slice().sort((a,b) => {
        let modifier = 1;
        if(this.currentSortDir === 'desc') modifier = -1;
        if(a[this.currentSort] < b[this.currentSort]) return -1 * modifier;
        if(a[this.currentSort] > b[this.currentSort]) return 1 * modifier;
        return 0;
      });
    },
  },
  mounted() {
    fetch(
      this.apiUrl + "getLogData/", {
      headers: {
        "Content-Type": "application/json",
      },
    })
    .then(response => response.json())
    .then((data) => {
      this.datas = data.datas;
    })
    .then(function() {
      stickybits('.fixHeader');
    })
  },
  methods: {
    isCurrent(n) {
      if (this.currentPage === n) {
        return true
      } else {
        return false
      }
    },
    sort: function (s) {
      //if s == current sort, reverse
      if(s === this.currentSort) {
        this.currentSortDir = this.currentSortDir==='asc'?'desc':'asc';
      }
      this.currentSort = s;
    },
  }
}
</script>

<style scoped>
div {
  margin: auto;
  border: 0;
  padding: 0;
}

table, td, tr {
  border: 1px solid #333;
}

td {
  padding: 5px;
}

th {
  border: 1px solid #333;
  background-color: #F7F5E6;
  color: #333A56;
}

.fixHeader {
  z-index: 15;
  background-color: #F7F5E6;
  color: #333A56;
}
</style>
