<template>
<div class="container">

<h3>神龍資料刷新，請輸入要刷新的"一級域名"</h3>

<div class="queryBox">
  <input v-model="refreshTld" onkeydown="if (event.keyCode == 13) document.getElementById('refresh').click()"/>
  <button id="refresh" class="btn btn-primary" style="font-size: 14px;" @click="data='正在刷新', refresh(refreshTld)" :disabled="refreshTld === ''">refresh</button>
</div>

<div class="refreshContent">
  <p v-if="data">{{data}}</p>
</div>

</div>
</template>

<script>
export default {
  name: 'refreshDragon',
  data () {
    return {
      refreshTld: '',
      data: null,
    }
  },
  mounted() {
  },
  methods: {
    refresh(tld) {
      this.refreshTld = '';
      fetch(
        this.apiUrl + "refresh/?domain=" + tld,
      )
      .then(response => response.json())
      .then((data) => {
        this.data = data;
        alert(tld + ' 刷新完畢');
      });
    }
  }
}
</script>

