<script>
export default {
  methods: {
    logout() {
      fetch(
      this.apiUrl + "dj-rest-auth/logout/", {
      method: 'POST',
      withCredentials: true,
      headers: {
        "Content-Type": "application/json",
      }
    })
      this.$cookie.removeCookie('auth');
      this.$router.push('/login');
    },
  }
}
</script>

