<template>
<!-- Vertically centered modal -->
<div class="modal fade" tabindex="-1" id="purchaseConfirm" aria-labelledby="purchaseConfirmLabel" aria-hidden="true">
  <div class="modal-dialog modal-dialog-centered">
    <div class="modal-content">
      <br>
      <div class="modal-header">
        <h5 class="modal-title" id="purchaseConfirmLabel" style="color: red">WARNING</h5>
      </div>
      <div class="modal-body">
        <br>
        <p>確定要買買買 <strong style="color: red">{{ purchaseDomain }}</strong> 的憑證嗎?</p>
        <img src="@/assets/takemymoney.jpg" alt="" width="360" height="180">
        <br>
        <br>
        <div class="form-check">
          <input class="form-check-input" type="checkbox" value=true v-model="confirm" id="purchaseConfirmCheckBox">
          <label class="form-check-label" for="purchaseConfirmCheckBox">
            確定
          </label>
          <br><br>
          <p v-if="doubleCheck === ''" style="color: red">請再輸入一次域名</p>
          <p v-if="doubleCheck !== '' && doubleCheck !== purchaseDomain" style="color: red">域名不一致</p>
          <div class="input-group mb-3">
            <span class="input-group-text" id="basic-addon1">域名確認</span>
            <input type="text" class="form-control" placeholder="" aria-label="" aria-describedby="basic-addon1" v-model="doubleCheck"/>
          </div>
        </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" style="margin-right: 5px;">先不要</button>
        <button type="button" class="btn btn-danger" @click="purchase()" :disabled="doubleCheck !== purchaseDomain || confirm === false" data-bs-dismiss="modal">買買買!!!</button>
      </div>
      <br>
    </div>
  </div>
</div>


<div class="container-fluid">
  <h3> 憑證購買，萬用憑證請用*.開頭 例: *.example.com </h3>

  <div class="row">
    <div class="queryBox">
      <input v-model="purchaseDomain" onkeydown="if (event.keyCode == 13) document.getElementById('doPurchase').click()" :disabled="onPurchase === true"/>
      <button id="doPurchase" class="btn btn-primary" style="font-size: 14px;" data-bs-toggle="modal" data-bs-target="#purchaseConfirm" :disabled="purchaseDomain === '' || onPurchase === true">買買買</button>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'certPurchase',
  data () {
    return {
      purchaseDomain: "",
      doubleCheck: "",
      confirm: false,
      onPurchase: false
    }
  },
  mounted() {
  },
  methods: {
    purchase() {
      this.onPurchase = true;
      let that = this;
      let data = {
        domain: this.purchaseDomain,
      }
      data = JSON.stringify(data);
      fetch(
        this.apiUrl + "certPurchase/", {
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
        },
        body: data
      })
      .then(function(response) {
        if (response.status === 401) {
          alert('權限不足');
        } else {
          alert('憑證: ' + that.purchaseDomain + ' 購買完成');
          that.onPurchase = false;
          that.purchaseDomain = "";
          that.doubleCheck = "";
          that.confirm =  false;
        }
      })
      .catch(error => {
        alert('Error:', error);
        that.onPurchase = false;
        that.purchaseDomain = "";
        that.doubleCheck = "";
        that.confirm =  false;
      });
    },
  }
}
</script>

